import React, {useState} from 'react';
import Recipe from './comp/Recipe';
function App() {
    const [recipes, setRecipes] = useState([]);
    let [expandedImg, setExpandedImg] = useState("")
    let [expandedCautions, setExpandedCautions] = useState([])
    let [expandedIngredients, setExpandedIngredients] = useState([])
    let [expandedSource, setExpandedSource] = useState("")
    let [expandedCalories, setExpandedCalories] = useState("");
    let [expandedLabel, setExpandedLabel] = useState("");
    let [expandedCuisine, setExpandedCuisine] = useState("");
    let [clickedBox, setClickedBox] = useState(false);
    let [url, setUrl] = useState("");
    let fetchResults = {};
    const {REACT_APP_URL_ID, REACT_APP_URL_KEY} = process.env;


    async function getReq(e) {
        document.querySelector("button").click();
        e.preventDefault();
        const response = await fetch(`https://api.edamam.com/search?q=${document.querySelector("input").value}&app_id=${REACT_APP_URL_ID}&app_key=${REACT_APP_URL_KEY}`)
        const data = await response.json()
        setRecipes(data.hits);
        document.querySelector(".results").scrollTop = 0;
        console.log(recipes)
    }


    let allRecipeData = recipes.map(obj => {
        fetchResults[obj.recipe.label] = {
            "image": obj.recipe.image,
            "cautions": obj.recipe.cautions,
            "cuisine": obj.recipe.cuisineType,
            "ingredients": obj.recipe.ingredientLines,
            "label": obj.recipe.label,
            "calories": Math.round(obj.recipe.calories),
            "source": obj.recipe.source,
            "url": obj.recipe.url
        }
        return <Recipe key={obj.recipe.calories} title={obj.recipe.label} image={obj.recipe.image} cautions={obj.recipe.cautions.length === 0 ? "None": obj.recipe.cautions.join(", ")} cuisine={obj.recipe.cuisineType ? obj.recipe.cuisineType.join(", ") : "Unkown"}/>
    })

    function main() {
        document.querySelectorAll(".box").forEach(invBox => {
            invBox.addEventListener("click", function() {
                setExpandedLabel(fetchResults[this.firstChild.innerHTML].label)
                setExpandedImg(fetchResults[this.firstChild.innerHTML].image)
                setExpandedCautions(fetchResults[this.firstChild.innerHTML].cautions)
                setExpandedIngredients(fetchResults[this.firstChild.innerHTML].ingredients)
                setExpandedSource(fetchResults[this.firstChild.innerHTML].source)
                setExpandedCalories(fetchResults[this.firstChild.innerHTML].calories)
                setExpandedCuisine(fetchResults[this.firstChild.innerHTML].cuisine)
                setUrl(fetchResults[this.firstChild.innerHTML].url)
                setClickedBox(true);
            })
        })
    }
    main()


    return (
        <div id="App">
            <form>
                <div>
                    <input type="text" name="search" id="search" placeholder="Search..."/>
                    <button onClick={getReq}><i className="fas fa-search"></i></button>
                </div>
            </form>
            <main>
                <div className="main_box">
                    <div className="results">
                        {allRecipeData}
                    </div>
                    <div className="resultExpandedContainer">
                        <div className="bgimg">
                            <img src={expandedImg} alt=""/>
                            <h1>{expandedLabel}</h1>
                            <h2>{clickedBox === true ? `By ${expandedSource}` : null}</h2>
                            <h3>{clickedBox === true ? expandedCuisine ? `Cuisine: ${expandedCuisine.join(", ")}` : "Cuisine: Unkown" : null}</h3>
                        </div>
                        <div className="cautionAndCalories">
                            <div className="cautionBox">
                            {clickedBox === true ? `Cautions: ${expandedCautions.length === 0 ? "None": expandedCautions.join(", ")}` : null}
                            </div>
                            <div className="caloriesBox">
                            {clickedBox === true ? `Calories: ${expandedCalories}` : null}
                            </div>
                        </div>
                        <div className="cautionAndCalories">
                            <div className="caloriesBox">
                            {clickedBox === true ? expandedIngredients.join(", "): null}
                            </div>
                        </div>
                        <div className="readmore">
                            {clickedBox === true ? <a href={url} target="blank">Read More</a> : null}
                        </div>
                    </div>
                </div>
            </main>
        </div>
    )
}

export default App
